* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding-top: 80px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

:root {
  --items: 4;
  --gap: 20px;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

.search-form {
  width: 100%;
  background-color: #144deb;
  justify-content: center;
  padding: 15px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form__input {
  height: 40px;
  border: none;
  border-radius: 10px 0 0 10px;
  outline: none;
  padding: 10px;
  font-size: 20px;
}

.search-form__btn {
  cursor: pointer;
  background-color: #ddd;
  background-image: url("load.c936e8bb.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  border: none;
  border-radius: 0 10px 10px 0;
  padding: 20px;
}

.search-form__btn:hover {
  background-color: #6a8dee;
}

.gallery {
  gap: var(--gap);
  flex-wrap: wrap;
  display: flex;
}

.photo-card__link {
  flex-basis: calc((100% - var(--gap) * (var(--items)  - 1) ) / var(--items) );
  justify-content: center;
  text-decoration: none;
  display: flex;
}

.photo-card__container {
  border-radius: 10px;
  flex-direction: column;
  display: flex;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, .035), 0 6.7px 5.3px rgba(0, 0, 0, .047), 0 12.5px 10px rgba(0, 0, 0, .06), 0 22.3px 17.9px rgba(0, 0, 0, .07), 0 41.8px 33.4px rgba(0, 0, 0, .086), 0 100px 80px rgba(0, 0, 0, .12);
}

.photo-card__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 10px 10px 0 0;
}

.info {
  flex-grow: 1;
  justify-content: center;
  gap: 20px;
  padding: 0 5px;
  display: flex;
}

.info-item {
  flex-direction: column;
  flex-basis: calc((100% - var(--gap) * (var(--items)  - 1) ) / var(--items) );
  align-items: center;
  margin: 10px 0;
  display: flex;
}

.load-more {
  min-width: 120px;
  min-height: 40px;
  color: #fff;
  cursor: pointer;
  background-color: #144deb;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 8px;
  font-size: 18px;
  display: block;
}

.load-more:hover {
  background-color: #6a8dee;
}

.is-hidden {
  display: none;
}

